import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { login } from 'Actions/userActions';
import PageLayout from 'Layouts/PageLayout/PageLayout';
import Button from 'Components/Button/Button';
import Input from 'Components/Input/Input';

import s from './AuthPage.module.sass';

class AuthPage extends PureComponent {
  state = {
    login: '',
    password: '',
  }

  handleChangeLogin = ({ target }) => (this.setState({ login: target.value }))

  handleChangePassword = ({ target }) => (this.setState({ password: target.value }))

  handleLogin = () => {
    const { login, password } = this.state;
    this.props.login(login, password);
  }

  render() {
    const { login, password } = this.state;
    console.log(this.props)
    const { profile, loginError } = this.props;
    if (profile && !loginError) {
      return <Redirect to="/admin/control" />
    }
    return (
      <PageLayout>
        <div className={s.container}>
          <div className={s.formContainer}>
            <div className={s.title}>Войти в кабинет управляюшего</div>
            <Input
              type="text"
              placeholder="Логин"
              value={login}
              onChange={this.handleChangeLogin}
            />
            <Input
              type="password"
              placeholder="Пароль"
              value={password}
              onChange={this.handleChangePassword}
            />
            <Button onClick={this.handleLogin} text="Войти" />
            {loginError && (
              <div className={s.error}>Ошибка авторизации</div>
            )}
          </div>
        </div>
      </PageLayout>
    );
  }
}

function mapStateToProps(state) {
  return {
    profile: state.user.profile,
    loginError: state.user.loginError,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    login: (userLogin, userPassword) => dispatch(login(userLogin, userPassword)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthPage);
