import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { getAllPlaces, searchPlace } from 'Actions/placesAction';
import PageLayout from 'Layouts/PageLayout/PageLayout';
import Input from 'Components/Input/Input';
import ShopItemCard from 'Components/ShopItemCard/ShopItemCard';
import s from './ShopListPage.module.sass';

class ShopListPage extends PureComponent {
  state = {
    search: '',
  }

  componentDidMount() {
    this.props.getAllPlaces();
  }

  handleChangeSearch = ({ target }) => {
    this.setState({ search: target.value });
    this.props.searchPlace(target.value);
  }

  render() {
    const { search } = this.state;
    const { placeList } = this.props;
    return (
      <PageLayout>
        <div className={s.container}>
          <div className={s.filterBlock}>
            Поиск заведении:
            <Input
              className={s.searchInput}
              type="text"
              placeholder="Например: Bazhov"
              value={search}
              onChange={this.handleChangeSearch}
            />
          </div>
          <div className={s.list}>
            {placeList.map(i => (
              <ShopItemCard {...i} key={i.id} />
            ))}
          </div>
        </div>
      </PageLayout>
    )
  }
}

function mapStateToProps(state) {
  return {
    placeList: state.places.renderPlaceList,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getAllPlaces: () => dispatch(getAllPlaces()),
    searchPlace: text => dispatch(searchPlace(text)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShopListPage);
