import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { getUserList, deleteUser, editUser, createUser } from 'Actions/userActions';
import { getAllPlaces } from 'Actions/placesAction';

import EditModal from 'Components/EditModal/EditModal';

import s from './UsersBlock.module.sass';

const userDataType = {
  login: 'Логин',
  name: 'Имя',
  password: 'Пароль',
  role: 'Роль',
  placeId: 'Заведение',
};

class UsersBlock extends PureComponent {
  state = {
    editModalOpen: false,
    userToEdit: null,
    fieldsToEdit: null,
  }

  componentDidMount() {
    this.props.getUserList();
    this.props.getAllPlaces();
  }
  
  toggleOpenEditModal = () => this.setState(prevState => ({
    editModalOpen: !prevState.editModalOpen,
  }))

  handleDeleteUser = ({ target }) => {
    const userId = target.dataset.id;
    this.props.deleteUser(Number(userId));
  }

  handlePreparEditUser = ({ target }) => {
    const userId = target.dataset.id;
    const user = this.props.userList.find(u => u.id === Number(userId));
    const editField = Object.keys(user)
      .map(field => ({
        field,
        value: user[field],
        label: userDataType[field],
      }));
    this.setState({
      userToEdit: this.props.userList.find(u => u.id === Number(userId)),
      editModalOpen: true,
      fieldsToEdit: editField,
    })
  }

  handleEditUser = (data) => {
    this.props.editUser(data)
  }

  handlePreparCreateUser = () => {
    const editField = Object.keys(userDataType)
      .map(field => ({
        field,
        value: '',
        label: userDataType[field],
      }));
    this.setState({
      userToEdit: null,
      editModalOpen: true,
      fieldsToEdit: editField,
    })
  }

  handleCreateUser = (data) => {
    this.props.createUser(data)
  }

  render() {
    return (
      <div>
        {!this.props.userList.length && (
          <div className={s.empty}>Пользовтелей нет. Создаите нового.</div>
        )}
        {this.props.userList.map(user => (
          <div key={user.login} className={s.userWrapper}>
            <div className={s.login}>Логин: {user.login}</div>
            <div className={s.place}>
              {!user.placeId ? 'Суперадмин' : <span>Заведение:&nbsp;</span>}
              <span className={s.placeName}>
                {this.props.placeList
                  .find(p => p.id === user.placeId)
                    ? this.props.placeList.find(p => p.id === user.placeId).title
                    : user.placeId}
              </span>
            </div>
            <button onClick={this.handlePreparEditUser} data-id={user.id} className={s.editButton}>Редактировать</button>
            <button onClick={this.handleDeleteUser} data-id={user.id} className={s.deleteButton}>Удалить</button>
          </div>
        ))}
        <div className={s.createBlock}>
          <button onClick={this.handlePreparCreateUser} className={s.createUser}>Создать пользователя</button>
        </div>
        <EditModal
          open={this.state.editModalOpen}
          onClose={this.toggleOpenEditModal}
          title={this.state.userToEdit ? 'Редактирование пользователя' : 'Создание пользователя'}
          successClick={this.state.userToEdit ? this.handleEditUser : this.handleCreateUser}
          fields={this.state.fieldsToEdit}
        />
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    userList: state.user.userList,
    placeList: state.places.renderPlaceList,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getUserList: () => dispatch(getUserList()),
    getAllPlaces: () => dispatch(getAllPlaces()),
    deleteUser: id => dispatch(deleteUser(id)),
    editUser: data => dispatch(editUser(data)),
    createUser: data => dispatch(createUser(data)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(UsersBlock);