const initialState = {
  bookingList: [],
};

export default function booking(state = initialState, action) {
  switch (action.type) {
    case 'BOOKING_TABLE_SUCCESS':
      return {
        ...state,
        bookingList: action.payload,
      };
    case 'GET_ALL_BOOKING':
      return {
        ...state,
        bookingList: action.payload,
      }
    case 'DELETE_BOOKING_SUCCESS':
      return {
        ...state,
        bookingList: action.payload,
      }
    default:
      return {
        ...state,
      };
  };
}
