import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { getPlaceById } from 'Actions/placesAction';
import { bookingTable } from 'Actions/bookingActions';
import PageLayout from 'Layouts/PageLayout/PageLayout';
import Input from 'Components/Input/Input';
import Dropdown from 'Components/Dropdown/Dropdown';
import Modal from 'react-responsive-modal';
import DatePicker from 'react-datepicker';
import ru from 'date-fns/locale/ru';
import 'react-datepicker/dist/react-datepicker.css'
import s from './ShopItemPage.module.sass';
import map1 from 'Assets/maps/maps/Map1.js';
import moment from 'moment';
import 'moment/locale/ru'
moment.locale('ru')

const tableOnMap = 12;
const mapData = Array(tableOnMap).fill(0).map((t, index) => ({
  id: index + 1,
  title: `Столик ${index + 1}`,
}));

class ShopItemPage extends PureComponent {
  state = {
    date: null,
    name: '',
    phone: '',
    tableId: null,
    modalIsOpen: false,
    error: false,
  }

  componentDidMount() {
    const placeId = Number(this.props.match.params.id);
    this.props.getPlaceById(placeId);
    this.tables = document.getElementById('Места');
  }

  toggleOpenModal = () => this.setState(prevState => ({
    modalIsOpen: !prevState.modalIsOpen,
  }))

  handleChooseTable = id => {
    this.setState({ tableId: id });
    const choosenTable = document.getElementsByClassName('choosen')[0];
    if (choosenTable) choosenTable.classList.remove('choosen');
    const table = document.getElementsByClassName(`table${id}`)[0];
    table.classList.add('choosen');
  }

  handleChangeDate = date => {
    this.setState({ date }, () => console.log(this.state.date));
    
  }

  handleChangeName = ({ target }) => this.setState({ name: target.value.trim() })
  handleChangePhone = ({ target }) => this.setState({ phone: target.value.trim() })

  handleBook = () => {
    const { date, name, phone, tableId } = this.state;
    this.setState({ error: !(date && name && phone && tableId) })
    this.toggleOpenModal();
    if (date && name && phone && tableId) {
      const placeId = this.props.match.params.id;
      this.props.bookingTable({
        ...{ date: moment(date).format('DD MMMM hh:mm') }, name, phone, tableId, ...{placeId: Number(placeId)},
      })
    }
  }

  render() {
    const {
      id, title, address, phone, avaragePrice, img, description,
    } = this.props.place;
    return (
      <PageLayout>
        <div className={s.bookingContainer}>
          <h2 className={s.title}>Забронируйте столик</h2>
          <div>
            <DatePicker
              selected={this.state.date}
              onChange={this.handleChangeDate}
              showTimeSelect
              timeFormat="HH:mm"
              timeIntervals={15}
              dateFormat="d MMMM, h:mm"
              timeCaption="Время"
              locale={ru}
              placeholderText="Выберите дату и время"
            />
            <Input
              className={s.input}
              placeholder="Ваше имя"
              value={this.state.name}
              onChange={this.handleChangeName}
            />
            <Input
              className={s.input}
              placeholder="Номер телефона"
              value={this.state.phone}
              onChange={this.handleChangePhone}
            />
          </div>
          <div className={s.mapWrapper}>
            <div className={s.map} dangerouslySetInnerHTML={map1()}></div>
          </div>
          <div className={s.chooseBlock}>
            Выберите столик:&nbsp;&nbsp;&nbsp;
            <Dropdown
              width="204px"
              data={mapData}
              onChange={this.handleChooseTable}
            />
          </div>
          <div className={s.buttonBlock}>
            <button onClick={this.handleBook}>Забронировать</button>
          </div>
        </div>
        <div className={s.infoContainer}>
          <div className={s.imageWrapper}>
            <img src={img} alt=""/>
          </div>
          <div className={s.body}>
            <div className={s.title}>{title}</div>
            <div>{description}</div>
            <div className={s.address}>{address}</div>
            <div className={s.phone}>{phone}</div>
            <div className={s.help}>Средний чек</div>
            <div className={s.average}>{avaragePrice} Р</div>
          </div>
        </div>
        <Modal
          open={this.state.modalIsOpen}
          onClose={this.toggleOpenModal}
        >
          <div className={s.modal}>
          {this.state.error ? (
            'Заполните все поля и не забудьте выбрать столик!'
          ) : (
            'Столик забронирован!'
          )}
          </div>
        </Modal>
      </PageLayout>
    )
  }
}

function mapStateToProps(state) {
  return {
    place: state.places.place,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getPlaceById: id => dispatch(getPlaceById(id)),
    bookingTable: data => dispatch(bookingTable(data)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShopItemPage);
