import { users } from './fakeData';

export function login(login, password) {
  return dispatch => {
    const userData = users.find(u => u.login === login && u.password === password);
    userData
    ? dispatch({
      type: 'LOGIN_SUCCESS',
      payload: userData,
    })
    : dispatch({
      type: 'LOGIN_FAILURE',
    });
  }
}

export function logout() {
  return dispatch => dispatch({
    type: 'LOGOUT',
  })
}

export function createUser({login, password, name, placeId, role}) {
  return (dispatch, getState) => {
    const { userList } = getState().user;
    const newId = userList.length ? userList[userList.length - 1].id  + 1: 1;
    userList.push({ login, password, name, ...{ placeId: Number(placeId) }, role, id: newId })
    dispatch({
      type: 'CREATE_USER_SUCCESS',
      payload: userList,
    })
  }
}

export function deleteUser(id) {
  return (dispatch, getState) => {
    const { userList } = getState().user;
    const newUserList = userList.filter(u => u.id !== id);
    dispatch({
      type: 'DELETE_USER_SUCCESS',
      payload: newUserList,
    })
  }
}

export function editUser(data) {
  return (dispatch, getState) => {
    const { userList } = getState().user;
    const newUserList = userList
      .map(u => u.id === data.id 
        ? { ...data, ...{ placeId: Number(data.placeId) } }
        : u);
    dispatch({
      type: 'EDIT_USER_SUCCESS',
      payload: newUserList,
    })
  }
}

export function getUserList() {
  return dispatch => {
    dispatch({
      type: 'GET_USER_LIST_SUCCESS',
      payload: users,
    })
  }
}