import React, { PureComponent } from 'react';
import cn from 'classnames';
import { connect } from 'react-redux';
import { login } from 'Actions/userActions';
import PageLayout from 'Layouts/PageLayout/PageLayout';

import UsersBlock from './UsersBlock/UsersBlock';
import PlacesBlock from './PlacesBlock/PlacesBlock';
import BookingBlock from './BookingBlock/BookingBlock';

import s from './AdminPanelPage.module.sass';

const tabs = ['Пользователи', 'Заведения', 'Бронирование'];

class AdminPanelPage extends PureComponent {
  state = {
    chooseTab: 2,
  }

  handleChooseTab = ({ target }) => {
    const chooseTab = target.dataset.tab;
    this.setState({
      chooseTab: Number(chooseTab),
    })
  }

  render() {
    return (
      <PageLayout>
        <div className={s.container}>
          <div className={s.tabBlock}>
            {tabs.map((title, index) => (
              <div
                data-tab={index}
                className={cn(s.tabItem, this.state.chooseTab === index && s.active)}
                onClick={this.handleChooseTab}
                key={title}
              >
              {title}
            </div>
            ))}
          </div>
          {this.state.chooseTab === 0 && <UsersBlock />}
          {this.state.chooseTab === 1 && <PlacesBlock />}
          {this.state.chooseTab === 2 && <BookingBlock />}
        </div>
      </PageLayout>
    );
  }
}

function mapStateToProps(state) {
  return {
    profile: state.user.profile,
    loginError: state.user.loginError,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    login: (userLogin, userPassword) => dispatch(login(userLogin, userPassword)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AdminPanelPage);
