import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { getAllBooking, deleteBooking } from 'Actions/bookingActions';

import s from './BookingBlock.module.sass';

class BookingBlock extends PureComponent {
  componentDidMount() {
    this.props.getAllBooking();
  }

  handleDeleteBooking = ({ target }) => {
    const bookingId = target.dataset.id;
    this.props.deleteBooking(Number(bookingId));
  }

  render() {
    return (
      <div>
        {!this.props.bookingList.length && (
          <div className={s.empty}>Нет забронированых столиков</div>
        )}
        {this.props.bookingList.map(booking => (
          <div key={booking.id} className={s.bookingWrapper}>
            <div className={s.column}>
              <div>Имя: {booking.name}</div>
              <div>Телефон: {booking.phone}</div>
            </div>
            <div className={s.column}>
              <div>Время: {booking.date}</div>
              <div>Столие: {booking.tableId}</div>
            </div>
            <div className={s.column}>
              <div>Заведение: {booking.placeId}</div>
            </div>
            <div className={s.column}>
            <button onClick={this.handleDeleteBooking} data-id={booking.id} className={s.deleteButton}>Удалить</button>
            </div>
          </div>
        ))}
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    bookingList: state.booking.bookingList,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getAllBooking: () => dispatch(getAllBooking()),
    deleteBooking: id => dispatch(deleteBooking(id)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(BookingBlock);