import { booking } from './fakeData';

export function bookingTable({placeId, date, name, phone, tableId}) {
  return (dispatch, getState) => {
    const { bookingList } = getState().booking;
    const newId = bookingList.length ? bookingList[bookingList.length - 1].id  + 1: 1;
    bookingList.push({ placeId, date, name, phone, tableId, ...{ id: newId } });
    dispatch({
      type: 'BOOKING_TABLE_SUCCESS',
      payload: [...bookingList],
    })
  }
}

export function getAllBooking() {
  return dispatch => {
    dispatch({
      type: 'GET_ALL_BOOKING',
      payload: booking,
    })
  }
}

export function getAllBookingByPlaceId(placeId) {
  return dispatch => {
    dispatch({
      type: 'GET_BOOKING_BY_PLACE_ID',
      payload: booking.filter(b => b.placeId === Number(placeId)),
    })
  }
}

export function deleteBooking(id) {
  return (dispatch, getState) => {
    const { bookingList } = getState().booking;
    const newBookingList = bookingList.filter(u => u.id !== id);
    dispatch({
      type: 'DELETE_BOOKING_SUCCESS',
      payload: [...newBookingList],
    })
  }
}
