import React from 'react';
import { Link } from 'react-router-dom';
import s from './ShopItemCard.module.sass';

const ShopItemCard = (props) => {
  const { id, title, address, phone, avaragePrice, img } = props;

  return (
    <Link to={`/shops/${id}`} className={s.card}>
      <div className={s.imageWrapper}>
        <img src={img} alt=""/>
      </div>
      <div className={s.body}>
        <div className={s.title}>{title}</div>
        <div className={s.address}>{address}</div>
        <div className={s.phone}>{phone}</div>
        <div className={s.help}>Средний чек</div>
        <div className={s.average}>{avaragePrice} Р</div>
      </div>
    </Link>
  )
};

export default ShopItemCard;
