const initialState = {
  placeList: [],
  renderPlaceList: [],
  place: {},
};

export default function places(state = initialState, action) {
  switch (action.type) {
    case 'GET_ALL_PLACES_SUCCESS':
      return {
        ...state,
        renderPlaceList: action.payload,
        placeList: action.payload,
      };
    case 'GET_PLACE_SUCCESS':
      return {
        ...state,
        place: action.payload,
      }
    case 'SEARCH_PLACE':
      return {
        ...state,
        renderPlaceList: action.payload,
      }
    case 'CREATE_PLACE_SUCCESS':
      return {
        ...state,
        renderPlaceList: action.payload,
        placeList: action.payload,
      }
    case 'DELETE_PLACE_SUCCESS':
      return {
        ...state,
        renderPlaceList: action.payload,
        placeList: action.payload,
      }
    case 'EDIT_PLACE_SUCCESS':
      return {
        ...state,
        renderPlaceList: action.payload,
        placeList: action.payload,
      }
    default:
      return {
        ...state,
      };
  };
}
