const initialState = {
  profile: null,
  userList: [],
  loginError: false,
};

export default function places(state = initialState, action) {
  switch (action.type) {
    case 'LOGIN_SUCCESS':
      return {
        ...state,
        profile: action.payload,
        loginError: false,
      };
    case 'LOGIN_FAILURE':
      return {
        ...state,
        loginError: true,
      }
    case 'LOGOUT':
      return {
        ...state,
      }
    case 'CREATE_USER_SUCCESS':
      return {
        ...state,
        userList: action.payload
      }
    case 'DELETE_USER_SUCCESS':
      return {
        ...state,
        userList: action.payload
      }
    case 'EDIT_USER_SUCCESS':
      return {
        ...state,
        userList: action.payload
      }
    case 'GET_USER_LIST_SUCCESS':
      return {
        ...state,
        userList: action.payload
      }
    default:
      return {
        ...state,
      };
  };
}
