import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import s from './Dropdown.module.sass';

class Dropdown extends PureComponent {
  state = {
    choosenValue: this.props.value,
    showList: false,
  }

  componentDidMount() {
    window.addEventListener('click', this.windowClick, true);
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.value !== nextProps.value) {
      this.setState({ choosenValue: nextProps.value });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.windowClick, true);
    window.removeEventListener('scroll', this.handleScroll);
  }

  onChooseItem = (e) => {
    this.setState({ choosenValue: Number(e.target.id), showList: false },
      () => this.props.onChange(this.state.choosenValue));
  }

  handleScroll = () => {
    this.setState({ showList: false });
  }

  windowClick = (e) => {
    if (!e.target.classList.contains(s.item) && !e.target.classList.contains(s.choosenValue)) {
      this.setState({ showList: false });
    }
  }

  toggleShowList = () => this.setState(prevState => ({
    showList: !prevState.showList,
  }));

  render() {
    const choosenItem = this.props.data.find(i => i.id === this.state.choosenValue);
    return (
      <div
        className={cn(s.dropdown, this.props.className)}
        style={{ width: this.props.width }}
      >
        <div
          className={cn(s.choosenValue,
            choosenItem && choosenItem.icon && s.choosenValueIcon,
            this.state.showList && s.openIcon,
            this.props.error && s.error,
            this.props.disabled && s.disabled)}
          onClick={this.toggleShowList}
          onKeyPress={this.toggleShowList}
          tabIndex="-1"
          role="button"
        >
          {choosenItem && choosenItem.icon
            && (
              <div
                className={s.icon}
                style={{ backgroundImage: `url(${choosenItem.icon})` }}
              />
            )
        }
          {choosenItem ? choosenItem.title : 'Выберите'}

        </div>

        <div
          className={cn(s.errorText, this.props.error && this.props.errorText && s.show)}
        >
          {this.props.error && this.props.errorText}
        </div>
        <div className={cn(
          s.list,
          !this.props.disabled && this.state.showList && s.open,
          this.props.data && this.props.data.length > 6 && s.withScroll,
        )}
        >
          {this.props.data && this.props.data.map(item => (
            <div
              id={item.id}
              key={item.id}
              className={s.item}
              onClick={this.onChooseItem}
              onKeyPress={this.onChooseItem}
              tabIndex="-1"
              role="button"
            >
              {item.icon && (
                <div
                  className={s.icon}
                  style={{ backgroundImage: `url(${item.icon})` }}
                />
              )}
              {item.title}
            </div>
          ))}
        </div>
      </div>
    );
  }
}

Dropdown.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.number,
    title: PropTypes.string,
    icon: PropTypes.string,
  })),
  width: PropTypes.string,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  onChange: PropTypes.func,
  className: PropTypes.string,
  error: PropTypes.bool,
  errorText: PropTypes.string,
  disabled: PropTypes.bool,
};

Dropdown.defaultProps = {
  data: [],
  width: '',
  value: 0,
  className: '',
  onChange: value => console.log('Empty onChange dropdown with value:', value),
  error: false,
  errorText: '',
  disabled: false,
};

export default Dropdown;
