import React, { PureComponent } from 'react';
import Modal from 'react-responsive-modal';

import s from './EditModal.module.sass';

class EditModal extends PureComponent {
  state = {}
  
  componentDidUpdate(prevProps) {
    if (this.props.fields !== prevProps.fields && this.props.fields) {
      const state = {};
      this.props.fields.forEach(f => state[f.field] = f.value);
      this.setState({...state})
    }
  }

  handleCansel = () => this.props.onClose();
  handleSuccess = () => {
    this.props.successClick(this.state);
    this.handleCansel();
  }

  handleChangeValue = ({ target }) => {
    const { value } = target;
    const field = target.dataset.field;
    this.setState({
      [field]: value,
    })
  }

  render() {
    return (
      <Modal
        open={this.props.open}
        onClose={this.props.onClose}
        blockScroll={false}
      >
        <div className={s.wrapper}>
        <div className={s.title}>{this.props.title}</div>
          {Object.keys(this.state).map(field => field !== 'id' && (
            <div key={field} className={s.row}>
              <div className={s.label}>
              {this.props.fields.find(f => f.field === field) && this.props.fields.find(f => f.field === field).label}
              </div>
              <input
                className={s.input}
                data-field={field}
                onChange={this.handleChangeValue}
                type="text"
                value={this.state[field]}
              />
            </div>
          ))}
          <div className={s.button}>
            <button onClick={this.handleSuccess} className={s.saveButton}>Сохранить</button>
            <button onClick={this.handleCansel} className={s.canselButton}>Отмена</button>
          </div>
        </div>
      </Modal>
    );
  }
}

export default EditModal;
