import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Header.module.sass';

const Header = () => {
  const firstActive = (_, location) => !location.pathname.includes('admin');

  return (
    <header className={s.header}>
      <div className={s.wrapper}>
        <h1 className={s.title}>TableBooking.</h1>
        <div className={s.tabs}>
          <NavLink to="/" className={s.tabsItem} isActive={firstActive} activeClassName={s.active}>Заведения</NavLink>
          <NavLink to="/admin" className={s.tabsItem} activeClassName={s.active}>Администрирования</NavLink>
        </div>
      </div>
    </header>
  );
};

export default Header;
