import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { getAllPlaces, deletePlace, editPlace, createPlace } from 'Actions/placesAction';

import EditModal from 'Components/EditModal/EditModal';

import s from './PlacesBlock.module.sass';

const placeDataType = {
  title: 'Заголовк',
  address: 'Адрес',
  phone: 'Телефон',
  avaragePrice: 'Средни чек',
  img: 'Ссылка на картинку',
  description: 'Описание'
};

class PlacesBlock extends PureComponent {
  state = {
    editModalOpen: false,
    placeToEdit: null,
    fieldsToEdit: null,
  }

  componentDidMount() {
    this.props.getAllPlaces();
  }

  toggleOpenEditModal = () => this.setState(prevState => ({
    editModalOpen: !prevState.editModalOpen,
  }))

  handleDeletePlace = ({ target }) => {
    const placeId = target.dataset.id;
    this.props.deletePlace(Number(placeId));
  }

  handlePreparEditPlace = ({ target }) => {
    const placeId = target.dataset.id;
    const place = this.props.placeList.find(u => u.id === Number(placeId));
    const editField = Object.keys(place)
      .map(field => ({
        field,
        value: place[field],
        label: placeDataType[field],
      }));
    this.setState({
      placeToEdit: this.props.placeList.find(u => u.id === Number(placeId)),
      editModalOpen: true,
      fieldsToEdit: editField,
    })
  }

  handleEditPlace = (data) => {
    this.props.editPlace(data)
  }

  handlePreparCreatePlace = () => {
    const editField = Object.keys(placeDataType)
      .map(field => ({
        field,
        value: '',
        label: placeDataType[field],
      }));
    this.setState({
      placeToEdit: null,
      editModalOpen: true,
      fieldsToEdit: editField,
    })
  }

  handleCreatePlace = (data) => {
    this.props.createPlace(data)
  }

  render() {
    return (
      <div>
        {!this.props.placeList.length && (
          <div className={s.empty}>Заведении ещё нет. Создате новое.</div>
        )}
        {this.props.placeList.map(place => (
          <div className={s.placeWrapper} key={place.id}>
            <div className={s.imageWrapper}>
              <img src={place.img} alt=""/>
            </div>
            <div className={s.body}>
                <div className={s.left}>
                  <div>Заголовк: {place.title}</div>
                  <div>Адрес: {place.address}</div>
                  <div>Телефон: {place.phone}</div>
                  <div>Среднии чек: {place.avaragePrice}</div>
                </div>
                <div className={s.right}>
                  <div className={s.description}>Описание: {place.description}</div>
                  <div className={s.buttons}>
                    <button onClick={this.handlePreparEditPlace} data-id={place.id} className={s.editButton}>Редактировать</button>
                    <button onClick={this.handleDeletePlace} data-id={place.id} className={s.deleteButton}>Удалить</button>
                  </div>
                </div>
              </div>
          </div>
        ))}
        <div className={s.createBlock}>
          <button onClick={this.handlePreparCreatePlace} className={s.createUser}>Создать заведение</button>
        </div>
        <EditModal
          open={this.state.editModalOpen}
          onClose={this.toggleOpenEditModal}
          title={this.state.placeToEdit ? 'Редактирование заведения' : 'Создание заведения'}
          successClick={this.state.placeToEdit ? this.handleEditPlace : this.handleCreatePlace}
          fields={this.state.fieldsToEdit}
        />
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    placeList: state.places.renderPlaceList,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getAllPlaces: () => dispatch(getAllPlaces()),
    deletePlace: id => dispatch(deletePlace(id)),
    editPlace: data => dispatch(editPlace(data)),
    createPlace: data => dispatch(createPlace(data)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(PlacesBlock);