import { fakePlaces, fakePlaceById } from './fakeData';

export function getAllPlaces() {
  return dispatch => dispatch({
    type: 'GET_ALL_PLACES_SUCCESS',
    payload: fakePlaces,
  });
}

export function getPlaceById(id) {
  return dispatch => dispatch({
    type: 'GET_PLACE_SUCCESS',
    payload: fakePlaceById(id),
  });
}

export function searchPlace(searchText) {
  return (dispatch, getState) => {
    const { placeList } = getState().places;
    return dispatch({
      type: 'SEARCH_PLACE',
      payload: placeList.filter(p => p.title.toLowerCase().includes(searchText)),
    });
  }
}

export function createPlace({title, address, phone, avaragePrice, img, description}) {
  return (dispatch, getState) => {
    const { placeList } = getState().places;
    const newId = placeList.length ? placeList[placeList.length - 1].id  + 1: 1;
    placeList.push({ title, address, phone, avaragePrice, img, description, id: newId })
    dispatch({
      type: 'CREATE_PLACE_SUCCESS',
      payload: [...placeList],
    })
  }
}

export function deletePlace(id) {
  return (dispatch, getState) => {
    const { placeList } = getState().places;
    const newPlaceList = placeList.filter(u => u.id !== id);
    dispatch({
      type: 'DELETE_PLACE_SUCCESS',
      payload: newPlaceList,
    })
  }
}

export function editPlace(data) {
  return (dispatch, getState) => {
    const { placeList } = getState().places;
    const newPlaceList = placeList
      .map(u => u.id === data.id ? data : u);
    dispatch({
      type: 'EDIT_PLACE_SUCCESS',
      payload: newPlaceList,
    })
  }
}